import React from 'react';
import { useEffect, useState } from 'react';

import { AdminView } from '../views';

import { PageEditor, indexStaffServices } from '@miq/editor/';

export default function IndexSettingsUpdateView(props) {
  const [page, setPage] = useState({});

  useEffect(() => {
    indexStaffServices
      .detail('current')
      .then((data) => setPage(data))
      .catch((err) => {});
  }, []);

  if (!page || !page.slug) return null;

  return (
    <AdminView title="Site HomePage Settings">
      <PageEditor slug={page.slug}>
        <PageEditor.Sections pageSlug={page.slug} sourceType="index" />
      </PageEditor>
    </AdminView>
  );
}
